import {
    AfterViewInit,
    Component,
    ElementRef,
    InjectionToken,
    Injector,
    Input,
    QueryList,
    SimpleChanges,
    TemplateRef,
    Type,
    ViewChild,
    ViewChildren,
} from '@angular/core';
import { BaseClass } from '@placeos/common';

import {
    applyGlobalStyles,
    createViewer,
    getViewer,
    Point,
    removeViewer,
    updateViewer,
    ViewAction,
    ViewerFeature,
    ViewerLabel,
    ViewerStyles,
} from '@placeos/svg-viewer';

export const MAP_FEATURE_DATA = new InjectionToken('Data for Map Features');

@Component({
    selector: `i-map,interactive-map`,
    template: `
        <div #outlet tabindex="0" role="map" class="absolute inset-0"></div>
        <mat-spinner
            *ngIf="!viewer || loading"
            class="center"
            [diameter]="48"
        ></mat-spinner>
        <div hidden *ngIf="injectors?.length">
            <ng-container *ngFor="let element of features; let i = index">
                <div *ngIf="element">
                    <div
                        #feature
                        class="pointer-events-none"
                        [attr.el-id]="element.location"
                        [attr.view-id]="viewer"
                    >
                        <ng-container [ngSwitch]="featureType(element.content)">
                            <ng-container *ngSwitchCase="'html'">
                                <div [innerHtml]="element.content"></div>
                            </ng-container>
                            <ng-container *ngSwitchCase="'template'">
                                <ng-container
                                    *ngTemplateOutlet="
                                        element.content;
                                        context: { data: element.data }
                                    "
                                ></ng-container>
                            </ng-container>
                            <ng-container *ngSwitchDefault>
                                <ng-container
                                    *ngComponentOutlet="
                                        element.content;
                                        injector: injectors[i]
                                    "
                                ></ng-container>
                            </ng-container>
                        </ng-container>
                    </div>
                </div>
            </ng-container>
        </div>
    `,
    styles: [
        `
            :host {
                height: 100%;
                width: 100%;
            }
        `,
    ],
})
export class InteractiveMapComponent
    extends BaseClass
    implements AfterViewInit {
    /** URL to the SVG file */
    @Input() public src: string;
    /** Custom CSS styles to apply to the SVG file */
    @Input() public styles: ViewerStyles;
    /** Zoom level to apply to the SVG */
    @Input() public zoom: number = 1;
    /** Zoom level to apply to the SVG */
    @Input() public center: Point = { x: 0.5, y: 0.5 };
    /** List of features to renderer over the SVG */
    @Input() public features: ViewerFeature[];
    /** List of labels to renderer over the SVG */
    @Input() public labels: ViewerLabel[];
    /** List of available user actions for the SVG */
    @Input() public actions: ViewAction[];

    public loading: boolean;

    public injectors: Injector[] = [];

    /** ID of the active SVG Viewer */
    public viewer: string;

    @ViewChild('outlet') private _outlet_el: ElementRef<HTMLDivElement>;
    @ViewChildren('feature') private _feature_list: QueryList<
        ElementRef<HTMLDivElement>
    >;

    public get feature_list() {
        return (this.features || [])
            .map((f, idx) => ({
                ...f,
                content: this._feature_list.toArray()[idx]?.nativeElement,
            }))
            .filter((f) => f.content);
    }

    constructor(private _injector: Injector) {
        super();
    }

    public ngOnInit() {
        applyGlobalStyles();
    }

    public ngOnDestroy(): void {
        if (this.viewer) {
            removeViewer(this.viewer);
        }
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.src && this.src) {
            this.createView();
        }
        if (this.viewer) {
            if (changes.zoom || changes.center) {
                this.updateDisplay();
            }
            if (
                changes.styles ||
                changes.features ||
                changes.labels ||
                changes.actions
            ) {
                this.injectors = (this.features || []).map((f: any) =>
                    Injector.create({
                        providers: [
                            {
                                provide: MAP_FEATURE_DATA,
                                useValue: { ...f.data },
                            },
                        ],
                        parent: this._injector,
                    })
                );
                this.timeout('update_view', () => this.updateView(), 100);
            }
        }
    }

    public ngAfterViewInit() {
        this.createView();
    }

    /** Get the type of the given content */
    public featureType(content: TemplateRef<any> | Type<any> | string) {
        if (typeof content === 'string') {
            return 'html';
        }
        if (content instanceof TemplateRef) {
            return 'template';
        }
        return 'component';
    }

    /** Update overlays, styles and actions of viewer */
    private updateView() {
        if (!getViewer(this.viewer) || this.loading) {
            return this.timeout('update_view', () => this.updateView());
        }
        try {
            updateViewer(this.viewer, {
                styles: this.styles,
                features: this.feature_list,
                labels: this.labels,
                actions: this.actions,
            });
        } catch (e) {}
    }

    /** Update zoom and center position of viewer */
    private updateDisplay() {
        updateViewer(this.viewer, {
            zoom: this.zoom,
            desired_zoom: this.zoom,
            center: this.center,
            desired_center: this.center,
        });
    }

    private async createView() {
        if (this.src && this._outlet_el?.nativeElement) {
            this.loading = true;
            if (this.viewer) {
                removeViewer(this.viewer);
            }
            this.viewer = await createViewer({
                element: this._outlet_el?.nativeElement,
                url: this.src,
                styles: this.styles,
                zoom: this.zoom,
                desired_zoom: this.zoom,
                center: this.center,
                features: this.feature_list,
                labels: this.labels,
                actions: this.actions,
            });
            this.timeout(
                'update_view',
                () => {
                    this.updateView();
                    this.updateDisplay();
                },
                100
            );
            this.loading = false;
        }
    }
}
